import { useState, useEffect } from 'react';
import { Badge } from '@/components/ui/badge';
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from '@/components/ui/table';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card';
import { firebase } from '@/lib/firebase';

interface FirebaseDevice {
    id: string;
    meterId: string;
    kwhr: number;
    status: string;
    tampering: string;
    lastCommunication: string;
}

const StatusBadge = ({ status }: { status: string }) => {
    const variant = status.toLowerCase() === 'online' ? 'default' : 'destructive';
    return <Badge variant={variant}>{status}</Badge>;
};

const TamperingBadge = ({ status }: { status: any }) => { // Change type to any for flexibility
    if (typeof status === 'string' && status.toLowerCase() === 'not tampered') { // Add type check
        return <Badge variant="default">Normal</Badge>;
    }
    // Handle cases where status is not a string or is not 'not tampered'
    return <Badge variant="destructive">{status ? String(status) : 'Unknown'}</Badge>; // Convert to string or show 'Unknown'
};


export default function DashboardPage() {
  const [meterData, setMeterData] = useState<FirebaseDevice[]>([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);

  useEffect(() => {
    const fetchData = async () => {
      try {
        const db = firebase.firestore();
        const metersCollection = db.collection('meters');
        const snapshot = await metersCollection.get();
        const data = snapshot.docs.map(doc => ({
          id: doc.id,
          ...doc.data() as FirebaseDevice // Type assertion for safety
        }));
        setMeterData(data);
      } catch (err: any) {
        setError(err.message);
        console.error("Error fetching meter data:", err);
      } finally {
        setLoading(false);
      }
    };

    fetchData();
  }, []);

  if (loading) {
    return <div>Loading...</div>;
  }

  if (error) {
    return <div>Error: {error}</div>;
  }

  return (
    <div className="container mx-auto py-10">
      <Card>
        <CardHeader>
          <CardTitle>Meter Data Dashboard</CardTitle>
          <CardDescription>Overview of connected smart meters and their status.</CardDescription>
        </CardHeader>
        <CardContent>
          <Table>
            <TableHeader>
              <TableRow>
                <TableHead>Meter ID</TableHead>
                <TableHead>Energy (kWh)</TableHead>
                <TableHead>Status</TableHead>
                <TableHead>Tampering Status</TableHead>
                <TableHead>Last Communication</TableHead>
              </TableRow>
            </TableHeader>
            <TableBody>
              {meterData.map((device) => (
                <TableRow key={device.id}>
                  <TableCell className="font-medium">{device.meterId}</TableCell>
                  <TableCell>{device.kwhr ? device.kwhr.toFixed(2) : '0.00'}</TableCell>
                  <TableCell><StatusBadge status={device.status} /></TableCell>
                  <TableCell><TamperingBadge status={device.tampering} /></TableCell>
                  <TableCell>{device.lastCommunication}</TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </CardContent>
      </Card>
    </div>
  );
}
